import React,{Component} from "react";

import Swiper from "../components/Swiper";
import Header from "../components/Header";
import Cell from "../components/Cell";
import Loading from '../components/Loading'

import axios from 'axios';
import {observer, inject} from 'mobx-react'
@inject('store')
@observer

class Home extends Component {
  constructor(props){
    super();
    //读取数据
    props.store.list.get({url: 'http://localhost:3000/api/swiper',propsName: 'swiper'});
    props.store.list.get({url: 'http://localhost:3000/api/banner',params:{_limit:4},propsName: 'banner'});
  }
  // state={
  //   cells:[],
  //   banners:[],
  //   loading:true
  // };

  render() {
    let {swiper,banner} = this.props.store.list;
    
    return (
      
      <div>
        {
         <Swiper {...this.props} banners={swiper} dataName="swiper"/>}
        
         <Cell cells={banner}  dataName="banner"/>
      </div>
      
    );
  }
  //  async componentDidMount(){

  //   let resSwiper = await axios({url:'http://localhost:3000/api/swiper'});
    
  //   this.setState({banners:resSwiper.data.data})

  //   axios({url:'http://localhost:3000/api/banner',params:{_limit:4}}).then(
  //     (res) => {
  //       let resBanner=res.data;
  //       this.setState({cells:resBanner.data,loading:false});

         
      
      
  //     }
  //   );
    

  // }
}

export default Home;